import divider from '@/components/divider.vue';

//引入网络请求
import $H from '@/common/request.js';
import $C from '@/common/config.js';
import $U from '@/common/util.js';


export default {
	components: {
		divider
	},
	data() {
		return {
			background: {
				backgroundColor: '#fff',
			},
			todyAdd: 0,
			todyOrder: 0,
			todyMoney: 0,
			monthAdd: 0,
			monthOrder: 0,
			monthMoney: 0,
			showPop:false,
			showDesc:false
		}
	},
	
	onLoad() {
		this.getData()
	}
	,
	methods: {
		
		
		getData(){
			// 发送到服务端
			$H.get("/activity_pt/tob/statistic").then((res) => {
				//请求成功
				console.log(res)
				
			}).catch((e) => {
				
				//请求失败
				console.log("失败"+e)
			})
		},
		
		
		
		toAllRecord(){
			uni.navigateTo({
				url:'../pintuanRecord/pintuanRecord'
			})
		},
		
		closeActivity(){
			this.showPop=true
		},
		
		
		showDescPop(show){
			
			this.showDesc=show
		},
		
		
		
		sureClose(){
			
		},
		toPoster(){
			uni.navigateTo({
				url:'../createPoster/createPoster'
			})
		}
	}
}
